@import url("https://cdn.jsdelivr.net/npm/@exampledev/new.css@1.1.2/new.min.css");

body {
  max-width: none;
}

.Controls {
  margin-bottom: 2rem;
}

.Controls > * {
  margin-right: 0.5rem;
}

.Grid {
  display: flex;
}

.Grid-cells {
  display: grid;
  grid-template: repeat(20, 30px) / repeat(30, 1fr);
  background-color: var(--nc-bg-1);
  border: 3px solid var(--nc-bg-3);
}

.Grid-cells > div {
  width: 30px;
  height: 30px;
  cursor: pointer;
  line-height: 30px;
  text-align: center;
  user-select: none;
  border: 1px solid var(--nc-bg-3);
}

.Grid-cells > div.visited {
  background-color: var(--nc-bg-2);
}
.Grid-cells > div.wall {
  background-color: var(--nc-bg-3);
}
.Grid-cells > div.solution {
  background-color: #99c6fa;
}
